@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+Mono:wght@500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'ClarityCity';
  font-weight: 400;
  src: local('ClarityCity'), url(/static/media/ClarityCity-Regular.61d25f87.woff) format('woff');
}

@font-face {
  font-family: 'ClarityCity';
  font-weight: 500;
  src: local('ClarityCity'), url(/static/media/ClarityCity-SemiBold.ee52c31a.woff) format('woff');
}


body {
  margin: 0px;
  padding: 0px;
  color: white;
  background-color: black;
  font-family: "ClarityCity";
  /* height: 5500px; */

  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

canvas {
  background: black;
  /* background: radial-gradient(circle, rgb(13, 9, 17) 40%, rgb(5, 5, 5) 100%); */
}

.App {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

canvas {
  touch-action: none;
}

.headerContainer {
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  width: 100vw;
  height: 100%;
  justify-content: center;
  pointer-events: none;
  transform-origin: top center;
  transform: scale(1);
  transition: transform 0.5s;
}

.scaledDown {
  transform-origin: bottom center;
  transform: scale(0);
  transition: transform 1s;
}

.footerContainer {
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  width: 100vw;
  justify-content: center;
}

a {
  color: white;
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

.pointing {
  cursor: pointer;
}

.progressContainer {
  position: fixed;
  left: 20px;
  bottom: 20px;
  width: 30px;
  margin: 5px;
}

.progress {
  height: 100%;
  background-color: white;
}

.iconContainer {
  width: 40px;
  height: 40px;
  margin-left: 10px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  pointer-events: all;
  transition: transform 0.1s;
}
.iconContainer:hover {
  transform: scale(1.15);
}


/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  /* display: none; */
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  /* -ms-overflow-style: none; */
  /* scrollbar-width: none; */
}
.console {
    margin: 2% 2vw;
    width: 96vw;
    width: 96vw;
    z-index: 2;
    height: 94%;
    height: 100%;
    background-color: rgba(15, 12, 19, 0.95);
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    border: 3px solid rgba(255, 255, 255, 0.025);
    /* backdrop-filter: blur(5px); */
}

.consoleHeader {
    width: calc(100% - 20px);
    height: 60px;
    padding-left: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.windowCircle {
    width: 15px;
    height: 15px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 50%;
    opacity: 0.7;
    margin-left: 10px;
}

.consoleContent {
    width: calc(100% - 60px);
    flex: 1 1;
    padding: 30px;
    padding-top: 20px;
    font-family: 'Noto Sans Mono';
    font-size: 16px;
    color: rgb(158, 158, 158);
    overflow: hidden;
}

.consoleLine {
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;
}

.Typist  {
    word-break: break-all;
}

.typed {
    color: rgb(221, 221, 221);
}

.highlighted {
    color: #ffa550;
    font-weight: 600;
}

.special {
    color: #3AA6EE;
    font-weight: 600;
}

.consoleLine a, .link {
    color: #ae8dfd;
    text-decoration: none;
    cursor: pointer;
    pointer-events: all !important;
}
.headerContent {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.textHeader {
    padding: 20px;
}

.titleBig {
    font-size: 50px;
    font-weight: 600;
}

.scrollIndicatorContainer {
    width: 30px;
    height: 40px;
    background-color: white;
    margin: 0 auto;
    margin-top: 100px;
    border-radius: 20%;
}
.staticFooterContainer {
    position: fixed;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 20px;
    pointer-events: none;
  }

.footerText {
    margin-bottom: 30px;
    font-size: 15px;
    font-weight: 400;
    color: rgba(255, 255, 255, 0.6);
    width: calc(100% - 140px);
    margin-left: 70px;
    margin-right: 70px;
    text-align: center;
}
.footerText a {
    color: rgba(255, 255, 255, 0.9);
    text-decoration: none;
}

.footerEmail {
    font-size: 12px;
    white-space: nowrap;
    position: absolute;
    bottom: 70px;
    right: 20px;
    color: rgba(255, 255, 255, 0.5);
}
.footerEmail span {
    color: rgba(255, 255, 255, 0.9);
    -webkit-user-select: all;
        -ms-user-select: all;
            user-select: all;
}


@media only screen and (max-width: 800px) {
    .staticFooterContainer {
        transform-origin: right center;
        right: 30px;
        bottom: -40px;
        transform: rotate(90deg);
    }
    .iconContainer svg {
        transform: rotate(-90deg);
    }
    .footerEmail {
        transform-origin: right bottom;
        transform: rotate(-90deg);
        margin-top: -30px;
        margin-right: 0px;
        position: relative;
        bottom: unset;
        right: unset;
    }
  }
